/** @jsx jsx */
import { css, jsx } from '@emotion/core';
import { FC } from 'react';
import { Link, graphql } from 'gatsby';
import Img, { FluidObject } from 'gatsby-image';

import Layout from './layout';
import Seo from './seo';
import { rhythm } from './styles/typography';
import Pagination from './pagination';

type Node = {
  id: string;
  path: string;
  slug: string;
  title: string;
  author: string;
  image: { sharp: { fluid: FluidObject } };
  parent: { excerpt: string };
};

type DevlogListTemplateProps = {
  data: {
    allDevlog: { nodes: Node[] };
  };
  pageContext: {
    limit: number;
    skip: number;
    numPages: number;
    currentPage: number;
  };
};

const DevlogListTemplate: FC<DevlogListTemplateProps> = ({ data, pageContext }) => (
  <Layout>
    <Seo title="Devlog" />

    <h1>Devlog</h1>

    <div>
      {data.allDevlog.nodes.map(({ id, image, title, parent, path }) => (
        <article
          css={css`
            border-bottom: 1px solid #ddd;
            display: flex;
            align-items: center;
            justify-content: space-between;
            margin-top: 0;
            padding: 1rem 0;
          `}
          key={id}
        >
          <Link
            to={path}
            css={css`
              min-width: 100px;
              display: none;

              @media screen and (min-width: 60em) {
                & {
                  display: initial;
                }
              }
            `}
          >
            <Img fluid={image.sharp.fluid} alt={title} />
          </Link>

          <div
            css={css`
              flex: 1;

              @media screen and (min-width: 60em) {
                & {
                  flex: 0.97;
                }
              }
            `}
          >
            <h3
              css={css`
                margin: 0 0 ${rhythm(0.7)};
              `}
              title={title}
            >
              <Link to={path}>{title}</Link>
            </h3>

            <p
              css={css`
                margin: 0;
              `}
            >
              {parent.excerpt}{' '}
              <Link
                css={css`
                  display: inline;
                `}
                to={path}
              >
                read more
              </Link>
            </p>
          </div>
        </article>
      ))}
    </div>

    <Pagination prefix="devlog" {...pageContext} />

    <Link
      css={css`
        display: inline-block;
      `}
      to="/"
    >
      &larr; back to home
    </Link>
  </Layout>
);

export const query = graphql`
  query($skip: Int!, $limit: Int!) {
    allDevlog(limit: $limit, skip: $skip, sort: { fields: date, order: DESC }) {
      nodes {
        id
        path
        slug
        title
        author
        image {
          sharp: childImageSharp {
            fluid(maxWidth: 100, maxHeight: 100) {
              ...GatsbyImageSharpFluid_withWebp
            }
          }
        }
        parent {
          ... on Mdx {
            excerpt(pruneLength: 80)
          }
        }
      }
    }
  }
`;

export default DevlogListTemplate;
